.post-content a.btn,
.post-share a.btn {
  box-shadow: none;
}
.post-share a.btn .svg-inline--fa,
.post-share a.btn:not(:last-child) {
  margin-right: 8px;
}
.btn {
  display: inline-block;
  margin-bottom: 0.25em;
  padding: 0.5em 1.25em;
  color: #fff !important;
  font-family: -apple-system, ".SFNSText-Regular", "San Francisco", "Roboto",
    "Segoe UI", "Helvetica Neue", "Lucida Grande", Arial, sans-serif;
  font-size: 0.85em;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  background-color: #7a8288;
  border: 0 !important;
  border-radius: 4px;
  cursor: pointer;
}
.btn:hover {
  background-color: #333;
}
.btn .icon {
  margin-right: 0.5em;
}
.btn .icon + .hidden {
  margin-left: -0.5em;
}
.btn--block {
  display: block;
  width: 100%;
}
.btn--block + .btn--block {
  margin-top: 0.25em;
}
.btn--inverse {
  color: #7a8288 !important;
  border: 1px solid #bdc1c4 !important;
  background-color: #fff;
}
.btn--inverse:hover {
  color: #fff !important;
  border-color: #7a8288;
}
.btn--light-outline {
  border: 1px solid #fff !important;
  background-color: transparent;
}
.btn--info {
  background-color: #52adc8;
}
.btn--info:hover {
  background-color: #428aa0;
}
.btn--warning {
  background-color: #f89406;
}
.btn--warning:hover {
  background-color: #c67605;
}
.btn--success {
  background-color: #62c462;
}
.btn--success:hover {
  background-color: #4e9d4e;
}
.btn--danger {
  background-color: #ee5f5b;
}
.btn--danger:hover {
  background-color: #be4c49;
}
.btn--disabled {
  pointer-events: none;
  cursor: not-allowed;
  filter: alpha(opacity=65);
  box-shadow: none;
  opacity: 0.65;
}
.btn--facebook {
  background-color: #3b5998;
}
.btn--facebook:hover {
  background-color: #2f477a;
}
.btn--twitter {
  background-color: #55acee;
}
.btn--twitter:hover {
  background-color: #448abe;
}
.btn--linkedin {
  background-color: #007bb6;
}
.btn--linkedin:hover {
  background-color: #006292;
}
